import { of } from "rxjs";
import { filter, mergeMap } from "rxjs/operators";
import { jobsSlice } from "../store/slices";
export const cancelJobdeleteJobEpic = (action$, _store$, { fs }) => action$.pipe(filter(jobsSlice.actions.cancel.match), mergeMap(({ payload: { jobId } }) => of(jobsSlice.actions.delete({ jobId }))));
