import { createSlice } from "@reduxjs/toolkit";
const initialConfigState = {
    current: {
        id: -1,
    },
};
export const tabsSlice = createSlice({
    name: "tabs",
    initialState: initialConfigState,
    reducers: {
        setTab(state, action) {
            state.current = action.payload.tab;
        },
    },
});
