import { of } from "rxjs";
import { filter, map, mergeMap } from "rxjs/operators";
import { playlistsSlice } from "../store/slices";
export const addPlaylistEpic = (action$, state$) => action$.pipe(filter(playlistsSlice.actions.addPlaylist.match), map((action) => action.payload), filter(({ id }) => Boolean(state$.value.playlists.playlists[id])), mergeMap(({ id }) => of(playlistsSlice.actions.fetchPlaylistLevels({
    playlistID: id,
}))));
